.login-form,
.login-form h1,
.login-form span,
.login-form input,
.login-form label {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}

.login-form {
  position: relative;
  width: 200px;
  height: 250px;
  padding: 15px 25px 0 25px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  cursor: default;
  
  background-color: #141517;
  
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  
  -webkit-box-shadow: 0px 1px 1px 0px rgba(255,255,255, .2), inset 0px 1px 1px 0px rgb(0,0,0);
  -moz-box-shadow: 0px 1px 1px 0px rgba(255,255,255, .2), inset 0px 1px 1px 0px rgb(0,0,0);
  box-shadow: 0px 1px 1px 0px rgba(255,255,255, .2), inset 0px 1px 1px 0px rgb(0,0,0);
} 

